import React from "react"

class Login extends React.Component{
    constructor(){
        super()
        this.state = {
            username: "",
            password: "",
            isLoggedIn: "true"
        }
    }
    render(){
        return(
            <section id="login">
                <div className="credentials">
                    <p>Demo login credentials:</p>
                    <p>Username: <b>admin</b></p>
                    <p>Password: <b>password</b></p>
                </div>
                <div className="form-container">
                    <form>
                        <label>Username</label><br />
                        <input type="text" onChange={this.props.checkUsername} value={this.props.usernameState} /><br />
                        <label>Password</label><br />
                        <input type="password" onChange={this.props.checkPassword} value={this.props.passwordState} /><br />
                    </form>
                </div>
                <div className="form-button-container"> 
                    <button onClick={this.props.handleClick}>LOGIN</button>
                </div>
            </section>
        )
    }
}

export default Login