import React from 'react';
import './css/style.css';
import Splash from "./components/Splash"
import Header from './components/Header'
import Orders from './components/Orders'
import Login from "./components/Login"

class App extends React.Component {
  constructor(){
    super()
    this.state = {
      isSplash: true,
      isLoggedIn: false,
      username: "",
      password: ""
    }
    this.unsplash = this.unsplash.bind(this)
    this.logRequest = this.logRequest.bind(this)
    this.checkUsername = this.checkUsername.bind(this)
    this.checkPassword = this.checkPassword.bind(this)
  }

unsplash(){
  this.setState({isSplash: false})
}
// Store all logic in parent component then pass down to child component through props for events
checkUsername(event){
  this.setState({username: event.target.value})
  console.log(this.state.username)
}

checkPassword(event){
  this.setState({password: event.target.value})
  console.log(this.state.password)
}

logRequest(){
  this.state.username === "admin" && this.state.password === "password" ? 
  this.setState({isLoggedIn: true})
  : 
  console.log("invalid")
}

  render(){
    const { isSplash, isLoggedIn } = this.state

    const showDashboard = isLoggedIn? 
    <Orders />
    :
    <Login 
      handleClick={this.logRequest} 
      checkUsername={this.checkUsername} 
      checkPassword={this.checkPassword} 
      usernameState={this.state.username} 
      passwordState={this.state.passwordState} 
    />

  const displayUsername = this.state.isLoggedIn? <h3>Hello {this.state.username }</h3>
    : "Login"

    const showSplash = isSplash? 
      <Splash handleClick={this.unsplash} /> 
      :       
      <div className="animated bounceInDown">
        <Header username={displayUsername} />
        {showDashboard}
      </div>

    return (
      <div className="App">
        {showSplash}
      </div>
    );
  }

}

export default App;
