import React from "react"

class Splash extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <section id="splash">
                <div className="splash-container">
                    <div className="content-container">
                        <h1 className="animated fadeInDown">Welcome</h1>
                        <hr />
                        <h3 className="animated fadeInDown">It is important to know that my demos and projects are being worked on daily, and as such are continous projects.</h3>
                        <h3 className="animated fadeInDown">This project is not yet fully responsive, please view at 1920x1080 resolution for best experience.</h3>
                        <button className="animated fadeInDown" onClick={this.props.handleClick}>I Understand</button>
                    </div>
                </div>
            </section>
        )
    }
}

export default Splash