import React from "react"
import Alert from "./Alert"
import Invoice from "./Invoice"
import ConfirmShipping from "./ConfirmShipping"

class Orders extends React.Component{
    constructor(){
        super()
        this.state = {
            users: [],
            displayConfirmShip: false,
            confirmShippingState: {},
            removeShippingIndex: 0,
            displayInvoice: false,
            invoiceDetails: {},
        }
        this.shipItem = this.shipItem.bind(this)
        this.printInvoice = this.printInvoice.bind(this)
        this.closeInvoice = this.closeInvoice.bind(this)
        this.openConfirmShip = this.openConfirmShip.bind(this)
        this.closeConfirmShip = this.closeConfirmShip.bind(this)
    }
componentDidMount(){
    fetch("https://jsonplaceholder.typicode.com/users")
    .then(request => request.json())
    .then(json => this.setState({users: json}))
}
shipItem(){
    const getState = this.state.users
    const getRemoveShippingIndex = this.state.removeShippingIndex
    console.log(getRemoveShippingIndex)
    getState.splice(getRemoveShippingIndex, 1)
    this.setState({users: getState, displayConfirmShip:false})
}
openConfirmShip(tableDataIndex){
    this.setState({displayConfirmShip:true, removeShippingIndex: tableDataIndex})
    const getState = this.state.users
    //set state so we can pass index of what was clicked to shipItem()
    this.setState({confirmShippingState: getState[tableDataIndex]})
    console.log(this.state.removeShippingIndex)
}
closeConfirmShip(){
    this.setState({displayConfirmShip:false})
}
printInvoice(tableDataIndex){
    const getState = this.state.users
    this.setState({invoiceDetails: getState[tableDataIndex]})
    this.setState({displayInvoice: true})
}
closeInvoice(){
    this.setState({displayInvoice: false})
}
    render(){
        const { users, displayConfirmShip, displayInvoice } = this.state
        const tableData = users.map((tableData, Index) => (
        <tr key={tableData.id}> 
            <td>{tableData.name}</td>
            <td>{tableData.email}</td>
            <td>{tableData.phone}</td>
            <td>{tableData.address.street}</td>
            <td>{tableData.address.suite}</td>
            <td>{tableData.address.city}</td>
            <td>{tableData.address.zipcode}</td>
            <td>
                <button onClick={() => this.openConfirmShip(Index) /*this.shipItem(Index)*/}>SHIP</button>  
            </td>
            <td>
                <button onClick={() => this.printInvoice(Index)}>Print</button>
            </td>           
        </tr>))
        const ordersToShip = users.length
        const shouldDisplayInvoice = displayInvoice? 
        <Invoice 
        //destructure
        name={this.state.invoiceDetails.name} 
        email={this.state.invoiceDetails.email}
        street={this.state.invoiceDetails.address.street}
        suite={this.state.invoiceDetails.address.suite}
        city={this.state.invoiceDetails.address.city}
        zip={this.state.invoiceDetails.address.zipcode}
        phone={this.state.invoiceDetails.phone}
        username={this.state.invoiceDetails.username}
        invoiceNo={this.state.invoiceDetails.id}
        btnHandle={this.closeInvoice}
        /> 
        : null

        const shouldDisplayConfirmShip = displayConfirmShip?
        <ConfirmShipping 
        handleClick={this.closeConfirmShip}
        name={this.state.confirmShippingState.name}
        street={this.state.confirmShippingState.address.street}
        suite={this.state.confirmShippingState.address.suite}
        city={this.state.confirmShippingState.address.city}
        zipcode={this.state.confirmShippingState.address.zipcode}
        
        confirmClick={this.shipItem}
        cancelClick={this.closeConfirmShip}/> 
        :
        null

        return(
        <div>
            <section className="animated fadeInUp" id="orders">
                {shouldDisplayConfirmShip}
                <Alert ordersToShip={ordersToShip} />
                    <div id="test">
                        <table align="center">
                            <thead>
                            <tr>
                                <td>Name</td>
                                <td>EMail</td>
                                <td>Phone</td>
                                <td>Street</td>
                                <td>Suite</td>
                                <td>City</td>
                                <td>Zipcode</td>
                                <td>Ship Item</td>
                                <td>Print Invoice</td>
                            </tr>
                            </thead>
                            <tbody>
                                {tableData}
                            </tbody>  
                        </table>
                    </div>
                </section>
                {shouldDisplayInvoice} 
            </div>
        )
    }
}

export default Orders