import React from "react"

class ConfirmShipping extends React.Component{
    constructor(){
        super()
    }

    render(){
        return(
            <section id="confirm-shipping">
                <div className="animated fadeInDown confirm-container">
                    <div className="confirm-body">
                        <h3><u>CONFIRM SHIPPING</u></h3>
                        <p>{this.props.name}</p>
                        <p>{this.props.street}</p>
                        <p>{this.props.suite}</p>
                        <p>{this.props.city}</p>
                        <p>{this.props.zipcode}</p>
                    </div>
                    <div className="confirm-header">
                        <button className="confirm-btn" onClick={this.props.confirmClick}>Confirm</button>
                        <button onClick={this.props.cancelClick}>Cancel</button>
                    </div>
                </div>
            </section>
        )
    }
}

export default ConfirmShipping